import { NavtiveCurrenyPage } from './navtive-curreny/navtive-curreny';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { CurrencySymbolPage } from './currency-symbol/currency-symbol';
import { SettingsProvider } from '../../providers/settings/settings';

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {
  hideBalance: boolean = false;

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public settingsProvider: SettingsProvider
              ) {
  }
  ionViewDidEnter() {
    if(this.settingsProvider.settings.headerPageIndex == 1) {
      this.hideBalance = true;
    }
  }

  ionViewDidLeave() {
    if(this.hideBalance) {
      this.settingsProvider.settings.headerPageIndex = 1;
    }
    else {
      this.settingsProvider.settings.headerPageIndex = 0;
    }
    this.settingsProvider.save();
  }

  navToCurrencySymbol(){
    this.navCtrl.push(CurrencySymbolPage);
  }

  navToNativeCurrency(){
    this.navCtrl.push(NavtiveCurrenyPage);
  }
}



